.header {
  display: flex;
  justify-content: space-around;
  position: fixed;
  width: 100%;
  box-shadow: 0.1rem 0.2rem 0.3rem rgba(0, 0, 0, 0.5);
  z-index: 100;
  top: 0;
  left: 0;
  background-color: rgba(66, 88, 175, 0.9);
}
.header h1 {
  color: #000000;
  text-align: center;
  margin: 0;
  padding: 2rem 0;
}
.header h1:hover {
  transition: 0.5s;
  color: #0D6EFD;
}
.header .abrir-menu,
.header .cerrar-menu {
  display: none;
}
@media (max-width: 1050px) {
  .header .abrir-menu,
  .header .cerrar-menu {
    display: block;
    border: 0;
    background-color: transparent;
    color: #000000;
    font-size: 3rem;
    cursor: pointer;
  }
  .header .navegacion-principal {
    opacity: 0;
    visibility: hidden;
    display: flex;
    align-items: end;
    gap: 1rem;
    flex-direction: column;
    position: fixed;
    color: #FFFFFF;
    align-items: start;
    background-color: #333;
    padding: 2rem;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5);
    height: 100vh;
    width: 100%;
    transform: translateX(100%);
  }
  .header .navegacion-principal a {
    color: #FFFFFF;
  }
  .header .navegacion-principal button {
    color: #FFFFFF;
  }
  .header .navegacion-principal i {
    color: #FFFFFF;
  }
  .header .nav-visible {
    opacity: 1;
    visibility: visible;
    transition: 0.5s;
    transform: translateX(0);
  }
}

.abrir-menu {
  display: none;
}

.navegacion-principal .nav__link__active {
  border-bottom: 2px solid #0D6EFD;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}
.navegacion-principal button {
  border: 0;
  background-color: transparent;
  color: #000000;
  font-size: 1.5rem;
  cursor: pointer;
  font-weight: 500;
}
.navegacion-principal button:hover {
  border-bottom: 2px solid #0D6EFD;
}
.navegacion-principal a {
  transition: 0.5s;
  display: block;
  text-align: center;
  color: #000000;
  font-size: 2rem;
  margin-bottom: 1rem;
  padding: 0.5rem 0;
  font-weight: 500;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  border-bottom: 2px solid #0D6EFD;
}

.switch {
  display: flex;
  width: 60px;
  height: 30px;
  justify-content: space-between;
  padding: 0 5px;
  align-items: center;
  position: relative;
  border-radius: 100px;
  border: #333 2px solid;
  background-color: #132b41;
}
.switch i {
  font-size: 1.3rem;
  color: #fdc21c;
}

.switch:last-child::before {
  content: "";
  position: absolute;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  background-color: #FFFFFF;
  border: solid thin #000000;
  left: 0;
  right: unset;
}

.switch:last-child.active::before {
  right: 0;
  left: unset;
  border: solid thin #FFFFFF;
}

.switch:last-child.active i:first-child {
  color: #FFFFFF;
}

.switch:last-child.active {
  background-color: #fdc21c;
  border: solid 2px #FFFFFF;
}

.idioma-toggle {
  background-color: transparent;
  border: 2px solid #0D6EFD;
  color: #000000;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 6px 12px;
  border-radius: 25px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease;
}
.idioma-toggle img {
  width: 24px;
  height: 24px;
}
.idioma-toggle i {
  font-size: 1rem;
  color: #0D6EFD;
}
.idioma-toggle:hover {
  background-color: #0D6EFD;
  color: white;
}
.idioma-toggle:hover i {
  color: white;
}